@use  '../../base/mixins' as m;
@use '../../base/variables' as v;

.barra{
    background-color: v.$azul;
    &__contenido{
        transition: all 300ms ease;
        @include m.contenedor;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        @include m.desktop {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo{
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem);
    }

    &__menu-mobil{
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 5rem;

        @include m.tablet {
            justify-content: center;
            gap: 0;
        }
        
        i{
            @include m.tablet {
                display: none;
            }
            display: block;
        }
    }

    &__btn-menu{
        all: unset;
        color: v.$blanco;
    }
    &__menu-animar{
        height: auto;
        overflow: visible;
        opacity: 1;
    }

    &__menu-cerrar{
        height: 0;
        overflow: hidden;
        opacity: 0;

        @include m.tablet {
            height: auto;
            opacity: 1;
            overflow: visible;
        }
    }
}
