@use  '../base/mixins' as m;
@use '../base/variables' as v;

.listado-ponentes{
    @include m.reset-lista();
    @include m.grid(1,1rem);
    margin-top: 1.5rem;
    &__ponente{
        color: v.$azul;
        border: 1px solid v.$azul;
        padding: 1rem;
        border-radius: 1rem;
        text-align: center;
        font-weight: 900;
        transition: all 300ms ease-in-out;
        &:hover{
            cursor: pointer;
            background-color: v.$azul;
            color: v.$blanco;
        }

        &--selec{
            background-color: v.$azul;
            color: v.$blanco;
        }
    }

    &__no-resultado{
        text-align: center;
        font-weight: 900;

    }
}