@use  '../base/mixins' as m;
@use '../base/variables' as v;


.paquetes{

    @include m.contenedor-md;
    @include m.tablet {
        @include m.contenedor;
    }

    &__grid{
        @include m.contenedor;
        @include m.grid(1,3rem);
        @include m.tablet {
            @include m.grid(3,3rem);
            align-items: flex-start
        }
    }

    &__submit{
        margin-top: 3rem;
        @include m.boton(v.$azul,v.$blanco);
        border-radius: 1.5rem;
    }
}


.paquete{
    @include m.card;

    &:nth-child(2){
        order: -1;
    }

    @include m.tablet {
        &:nth-child(2){
            order: unset;
        }
    }
    
    &__lista{
        @include m.reset-lista;
        line-height: 2;
    }

    &__precio{
        font-size: 6rem;
        color: v.$azul;
        font-weight: 900;
        text-align: center;
        margin: 3rem 0;
    }
}