@use  '../base/mixins' as m;
@use '../base/variables' as v;

.dashboard{
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$gris-claro;

    &__header{
        background-color: v.$gris-oscuro;
        padding: 2rem;
    }

    &__header-grid{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__logo{
        @include m.logo;
        @include m.texto-fluido(2rem, 3rem);
    }
    
    &__form{
        display: flex;
        justify-content: center;
    }

    &__submit--logout{
        @include m.enlace;
    }

    &__grid{
        display: flex;
        
        flex-direction: column;
        @include m.tablet {
            flex-direction: row;
            flex: 1;
            overflow-y: auto;
        }
        
    }

    &__sidebar{
        background-color: lighten(v.$gris-oscuro,15);
        width: 100%;
        overflow-x: auto;
        overflow-y: hidden;
        @include m.tablet {
            width: 12rem;
        }
    }

    &__menu{
        padding: 1rem;
        display: flex;
        flex-direction: row;
        @include m.tablet {
            flex-direction: column;  
            
        }
        gap: 2rem;
        justify-content: space-between;
        
    }

    &__enlace{
        color: lighten(v.$gris-oscuro,35);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: all 300ms ease-in-out;
        &:hover{
            background-color: lighten(v.$gris-oscuro,20);
            color: v.$blanco;
            border-radius: 1.5rem;
        }

        &--actual{
            background-color: lighten(v.$gris-oscuro,20);
            color: v.$blanco;
            border-radius: 1.5rem;
        }
    }

    &__icono{
        font-size: 2rem;
    }

    &__menu-texto{
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: 900;
        margin-top: .5rem;
    }

    &__contenido{
        width: 100%;
        overflow-y: scroll;
        padding: 1rem 1rem 3rem 1rem;
        @include m.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }

    &__contenedor-boton{
        @include m.tablet {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 5rem
        }
    }

    &__boton{
        @include m.boton(v.$azul, v.$blanco);
        border-radius: 1.5rem;
        text-transform: uppercase;
        font-weight: 900;
        font-size: 1.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 1rem;
        margin: 5rem 0;
        @include m.tablet {
            width: auto;
            margin: 0
        }
    }   

    &__contenedor{
        @include m.sombra;
        background-color: v.$blanco;
        border-radius: 1rem;
        padding: 1rem;
        @include m.tablet {
            padding: 2rem
        }
    }

    &__formulario{
        @extend .dashboard__contenedor;
        @include m.contenedor-md;
    }

    &__grafica{
        @include m.contenedor-sm;
    }
}