@use  '../base/mixins' as m;
@use '../base/variables' as v;

.eventos-registro{
    @include m.contenedor;
    
    @include m.tablet {
        display: flex;
        align-items: flex-start;
        gap: 2.5rem;
    }
    &__listado{
        @include m.tablet {
            flex: 0 0 60%;
        }
    }

    &__grid{
        @include m.grid(1,3rem);

        @include m.tablet {
            @include m.grid(2,3rem);
        }
    }

    &__heading{
        &--conferencias,
        &--workshops{
            @include m.texto-fluido(2rem, 6rem);
            white-space: nowrap;
        }

        &--conferencias{
            color: v.$azul;  
        }

        &--workshops{
            color: v.$secundario;
            margin-top: 5rem;
        }
    }

    &__fecha{
        margin: 6rem 0 3rem 0;
    }
}

.registro-aside{
    background-color: v.$gris-claro;
    padding: 3rem;
    border-radius: 1rem;
    @include m.sombra;

    @include m.tablet {
        flex: 0 0 40%;
        position: sticky;
        position: -webkit-sticky;
        top: 3rem;
    }

    &__heading{
        margin: 0 0 3rem 0;
    }

    &__evento{
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 2rem;
        flex-direction: column;
        margin-bottom: 1rem;

        &:nth-child(even){
            background-color: darken(v.$gris-claro, 3);
        }   

        &:last-of-type{
            margin-bottom: 0;
        }

        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__nombre{
        font-size: 1.8rem;
        margin: 1rem 0 1rem 1rem;
    }

    &__eliminar{
        all: unset;
        padding: 1rem;
        color: darken(red, 10);
        
        &:hover{
            cursor: pointer;
        }
    }

    &__no-evento{
        font-size: 1.5rem;
        text-align: center;
        color: v.$gris-oscuro;
    }

    &__regalo{
        margin-top: 3rem;
    }
    &__label{
        display: block;
        font-size: 2.2rem;
        font-weight: 900;
        margin-bottom: 1rem;
    }

    &__select{
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        padding: 1rem;
        border-radius: .5rem;
    }
}
