@use  '../../base/mixins' as m;
@use '../../base/variables' as v;

.footer{
    background-color: v.$azul;
    margin-top: 5rem;
    padding-top: 3rem;
    color: v.$blanco;

    &__grid{
        @include m.contenedor;
        @include m.tablet {
            @include m.grid(2,3rem);
        }
    }

    &__logo{
        @include m.logo;
        @include m.texto-fluido(2rem,4rem);
    }

    &__texto{
        line-height: 1.5;
        text-align: center;
        @include m.tablet {
            text-align: left;
        }
    }

    &__copy{
        background-color: darken(v.$azul,10);
        margin: 3rem 0 0 0;
        padding: 2rem 0;
        text-align: center;

        font-size: 1.5rem;
        font-weight: v.$black;

        &--regular{
            font-weight: v.$regular;
        }
    }
    &__pgcode{
        font-size: 1.5rem;
        text-decoration: none;
        color: v.$blanco;
        font-weight: 900;
    }
}