@use  '../base/mixins' as m;
@use '../base/variables' as v;

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    @include m.tablet {
        justify-content: flex-end;
    }
    &__enlace{
        color: v.$azul;
        font-size: 1.6rem;
        &--texto{
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;
            font-weight: 900;
            transition: all 300ms ease-in-out;
            &:hover{
                background-color: v.$azul;
                color: v.$blanco;
            }
        }

        &--numeros{
            font-weight: 900;
            transition: all 300ms ease-in-out;
            display: none;
            @include m.tablet {
                display: inline;
            }
            &:hover{
                color: v.$gris;
            }
        }
        &--actual{
            color: v.$gris;
            font-weight: 900;
            text-decoration: underline;
            display: none;
            @include m.tablet {
                display: inline;
            }
        }
    }
}