@use  '../base/mixins' as m;
@use '../base/variables' as v;


.agenda{
    padding: 5rem 0;
    @include m.contenedor;


    &__contenido{
        padding: 2.5rem;
    }

    &__fecha, &__lugares{
        font-size: 1.5rem;
        color: v.$gris-oscuro;
    }

    &__texto{
        line-height: 1.5;
    }

    &__imagen-autor{
        width: 20rem;
        background-color: v.$azul;
        border-radius: 50%;
        padding: 1.5rem;
        &--workshop{
            @extend .agenda__imagen-autor;
            background-color: v.$secundario;
        }
    }
    &__orador{
        @include m.contenedor-md;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 5rem;
        gap: 2rem;
        @include m.sombra;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-evenly;
        }
        margin-bottom: 5rem;
    }

    &__datos{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    &__nombre-orador{
        font-weight: 900;
        color: v.$azul;

        &--workshop{
            @extend .agenda__nombre-orador;
            color: v.$secundario;
        }
    }

    &__listado-skills{
        @include m.tags;

        &--workshop{
            @include m.tags-secundario;
        }
    }

    &__enlace-volver{
        padding: 1.5rem;
        border-radius: 1.5rem;
        background-color: v.$azul;
        color: v.$blanco;
        font-weight: 900;
        text-transform: uppercase;
        transition: all 300ms ease;
        &:hover{
            background-color: darken(v.$azul, 10);
        }
    }
}

.eventos{
    $mod: &;

    &__heading{
        color: v.$azul;
        margin: 5rem 0;
        text-align: left;
        font-size: clamp(2rem, -0.8rem + 8vw, 6rem);
    }

    &__fecha{
        color: v.$gris;
        margin: 3rem 0 1rem 0;
    }

    &--workshops{
        #{$mod}__heading{
            color: v.$secundario;
        }

        .evento__info{
            background-color: v.$secundario;

            &:hover{
                background-color: darken(v.$secundario,10%);
            }
        }
    }

    .swiper-button-next,
    .swiper-button-prev{
        color: rgba(v.$blanco, .5);
    }
}

.evento{
    &__grid{
        display: flex;
        align-items: center;
        gap: 1.5rem;
        justify-content: space-between;
    }
    &__hora, &__disponible{
        font-weight: 900;
    }

    &__nombre{
        text-decoration: none;
        color: v.$blanco;
        font-weight: 900;
        font-size: 2rem;
        margin: 0;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }
    &__info{
        padding: 2rem;
        border-radius: 1rem;
        background-color: v.$azul;
        color: v.$blanco;

        transition: all 300ms ease;
        &:hover{
            background-color: darken(v.$azul, 10%);
        }
    }

    &__autor-info{
        display: flex;
        gap: 2rem;
        justify-content: space-between;
        align-items: center;
    }
    &__imagen-autor{
        width: 5rem;
        background-color: v.$blanco;
        border-radius: 50%;
    }

    &__autor-nombre{
        font-weight: 900;
        font-size: 1.5rem;
    }

    &__intro{
        font-size: 1.5rem;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &__agregar{
        @include m.boton(v.$blanco, v.$negro);
        margin-top: 1.5rem;
        display: flex;
        align-items: center;
        gap: 2rem;
        justify-content: center;
        text-transform: uppercase;
        font-weight: 900;

        &:disabled{
            opacity: .5;
            cursor: not-allowed;
        }
    }
}