@use  '../base/mixins' as m;
@use '../base/variables' as v;

.devwebcamp{


    &__grid{
        @include m.contenedor;
        @include m.grid(1,3rem);
        @include m.tablet {
            @include m.grid(2,5rem);
            align-items: center;
        }
    }

    &__imagen{

        picture{
            source, img{
                border-top-left-radius: 2rem;
                border-bottom-right-radius: 2rem;
            }
        }
        transition: all 300ms ease-in-out;
        &:hover{
            scale: 1.02;
        }
    }
    &__texto{
        line-height: 1.5;
    }   
}