@use  '../base/mixins' as m;
@use '../base/variables' as v;

.table{

    &__contenedor{
        overflow-y: scroll;
        @include m.tablet {
            overflow-y: hidden;
        }
    }
    width: 100%;
    border-collapse: collapse;
    
    &__thead{
        background-color: v.$gris-oscuro;
        color: v.$blanco;
    }

    &__th{
        padding: 1rem;
        text-align: center;
    }

    &__tr{
        &:nth-of-type(even){
            background-color: darken(v.$gris-claro,5);
        }
    }

    &__td{
        text-align: center;
        @include m.tablet {
            text-align: left;
        }
        padding: 1.5rem 1rem;

        &--acciones{
            @extend .table__td;
            display: flex;
            @include m.tablet {
                flex-direction: row;
                justify-content: flex-end;
            }
            justify-content: center;
            flex-direction: column;
            align-items: center;
            gap: 2rem;
        }
    }

    &__accion{
        all: unset;
        text-transform: uppercase;
        font-size: 1.5rem;
        font-weight: 900;

        &--editar{
            color: v.$azul;
        }

        &--eliminar{
            color: v.$rojo;
        }

        &:hover{
            cursor: pointer;
        }
    }
}