@use  '../../base/mixins' as m;
@use '../../base/variables' as v;

.formulario{
    display: flex;
    flex-direction: column;

    &__campo{
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;
    }

    &__contenedor-icono{
        display: flex;
        border-radius: 1rem;
        overflow: hidden;
    }

    &__icono{
        background-color: v.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        i{
            color: v.$blanco;
            font-size: 2rem;
        }
    }

    &__label{
        display: block;
        font-size: 1.8rem;
        color: rgba(v.$negro, .8);
    }

    &__input, &__select{
        padding: 1rem;
        border: 1px solid darken(v.$gris-claro, 20);
        border-radius: 1rem;

        &--file{
            border: none;
            padding: 1rem 0;
        }

        &--social{
            @extend .formulario__input;
            flex: 1;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }

    &__radio{
        display: flex;
        align-items: center;
        gap: 2rem;

        &--descripcion{
            text-transform: uppercase;
            display: flex;
            align-items: center;
            gap: 1rem;
        }
    }

    &__info{
        font-size: 1.2rem;
        margin: 0;
        padding: 0;
        font-weight: 700;
    }

    &__submit{
        @include m.boton(v.$azul,v.$blanco);
        border-radius: .5rem;

        @include m.tablet {
            width: auto;
            align-self: flex-end;
        }

        &--full{
            width: 100%;
        }
        
    }

    &__fieldset{
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend{
        color: v.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;
    }

    &__listado{
        @include m.tags;
    }

    &__texto{
        margin-top: 3rem;
    }

    &__imagen{
        max-width: 20rem;
    }
}